import logo from './logo.svg';
import './App.css';
import {Button, Container, Grid, Typography, AppBar, Box, Link} from "@mui/material";
import React, {useState} from "react";
const ethers = require("ethers");

function App() {
  const [display, setDisplay] = useState("none")
  const [createAgain, setCreateAgain] = useState(false)
  const [mnemonic, setMnemonic] = useState("")
  const [address, setAddress] = useState("")
  const [pKey, setPKey] = useState("")
  const [showPKey, setShowPKey] = useState("none")

  const createWallet = async () => {
    console.log("wallet created.1")
    const wallet = ethers.Wallet.createRandom()
    setAddress(wallet.address)
    setMnemonic(wallet.mnemonic.phrase)
    setPKey(wallet.privateKey)
    setDisplay("")
    setCreateAgain(true)
    console.log("wallet created.")
  }

  const showPKeyCb = async () => {
    setShowPKey("");
  }

  return (
    <Container sx={{width: '100%'}}>
      <AppBar position="static" style={{ background: 'transparent', boxShadow: 'none'}}>
        <Typography color="black" sx={{marginTop: '10px', fontWeight: 'bold'}}>
          
        </Typography>
      </AppBar>
      <Container align="center" sx={{width: '100%'}}>
        <Grid container spacing={2} sx={{marginTop: "20%"}} align="center">
          <Grid item xs={12}>
            <Typography variant='p'>
              , 
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Button variant="outlined" onClick={createWallet} > {createAgain? "" : ""}</Button>
          </Grid>
          <Grid item xs={12} display={display}>
            <Typography variant='p' sx={{fontWeight:'bold'}}>
              : {address}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Typography variant='p' display={display} sx={{fontWeight:'bold'}}>
              : {mnemonic}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Typography variant='p' display={display} color="red" sx={{fontWeight:'bold'}}>
              : , , . , , .
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Typography variant='p' display={display} color="red" sx={{fontWeight:'bold'}}>
              : , , 
            </Typography>
          </Grid>
          <Grid item xs={12} display={display}>
            <Button variant="outlined" onClick={showPKeyCb}> </Button>
          </Grid>
          <Grid item xs={12}>
            <Typography variant='p' display={showPKey} sx={{fontWeight:'bold'}}>
              : {pKey}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Typography variant='p'>
              
            </Typography>
          </Grid>
          <Grid item xs={12} sx={{alignItems: 'center'}}>
          <Link href="https://metamask.io/">
          <Box
            component="img"
            sx={{
              width: "5%",
              margin: "10px"
            }}
            src="imgs/metamask-fox.svg"
          />
          </Link>
          <Link href="https://www.coinbase.com/wallet">
          <Box
            component="img"
            sx={{
              width: "5%",
              margin: "10px"
            }}
            src="imgs/walletlink-alternative.webp"
          />
          </Link>
          <Link href="https://docs.kaikas.io/">
          <Box
            component="img"
            sx={{
              width: "5%",
              margin: "10px"
            }}
            src="imgs/kaikas-alternative.webp"
          />
          </Link>
          </Grid>
        </Grid>
      </Container>
    </Container>
  );
}

export default App;
